<template>
  <div class="mindMapContainer">
    <div id="mindMap"></div>
    <div class="controlContainer">
      <div class="controlItem" v-for="item in newControlList" :key="item.iconClass" :title="item.title" @click="item.onClick">
        <i :class="item.iconClass"></i>
        <span>{{ item.text }}</span>
      </div>
    </div>
    <div class="shortcutContainer">
      <div class="shortcutContent" v-if="!isReadonly && isShortcut">
        <div class="shortcut" v-for="item in shortcutList" :key="item.text">
          <p>
            <i class="el-icon-info"></i>
            <label>{{ item.text }}</label>
          </p>
          <span>{{ item.shortcut }}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MindMap from 'simple-mind-map'
import { mindMapData } from './mindMapData'
export default {
  data() {
    return {
      mindMapData: mindMapData,
      mindMap: null,
      isReadonly: false, //是否只读
      isShortcut: true, //是否显示快捷键
      shortcutList: [
        {
          text: '插入下级节点',
          shortcut: 'Tab | Insert',
        },
        {
          text: '插入同级节点',
          shortcut: 'Enter',
        },
        {
          text: '插入父节点',
          shortcut: 'Shift + Tab',
        },
        {
          text: '上移节点',
          shortcut: 'Ctrl + ↑',
        },
        {
          text: '下移节点',
          shortcut: 'Ctrl + ↓',
        },
        {
          text: '展开/收起节点',
          shortcut: '/',
        },
        {
          text: '删除节点',
          shortcut: 'Delete | Backspace',
        },
        {
          text: '仅删除当前节点',
          shortcut: 'Shift + Backspace',
        },
        {
          text: '复制节点',
          shortcut: 'Ctrl + C',
        },
        {
          text: '剪切节点',
          shortcut: 'Ctrl + X',
        },
        {
          text: '粘贴节点',
          shortcut: 'Ctrl + V',
        },
        {
          text: '编辑节点',
          shortcut: 'F2',
        },
        {
          text: '文本换行',
          shortcut: 'Shift + Enter',
        },
        {
          text: '回退',
          shortcut: 'Ctrl + Z',
        },
        {
          text: '前进',
          shortcut: 'Ctrl + Y',
        },
        {
          text: '全选',
          shortcut: 'Ctrl + A',
        },
        {
          text: '多选',
          shortcut: 'Ctrl + 左键',
        },
        {
          text: '放大',
          shortcut: 'Ctrl + +',
        },
        {
          text: '缩小',
          shortcut: 'Ctrl + -',
        },
        {
          text: '放大/缩小',
          shortcut: 'Ctrl + 鼠标滚动',
        },
        {
          text: '回到根节点',
          shortcut: 'Ctrl + Enter',
        },
        {
          text: '适应画布',
          shortcut: 'Ctrl + i',
        },
      ],
    }
  },
  computed: {
    mindMapConfig() {
      return {
        pluginList: [], //当前注册的所有插件列表
        alwaysShowExpandBtn: false, //是否一直显示节点的展开收起按钮，默认为鼠标移上去和激活时才显示
        mouseScaleCenterUseMousePosition: false, //鼠标缩放是否以鼠标当前位置为中心点，否则以画布中心点
        fit: true, //是否自动适配画布大小
        initRootNodePosition: ['left', 'top'], //缩放初始根节点的位置
        noteIcon: {
          icon: '<svg t="1740044024079" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5860" width="200" height="200"><path d="M504.5 101.77c-226.563 0-410.229 183.666-410.229 410.229 0 226.565 183.666 410.231 410.229 410.231s410.229-183.666 410.229-410.231c0-226.563-183.666-410.229-410.229-410.229z m0 755.048c-190.437 0-344.818-154.381-344.818-344.819 0-190.438 154.381-344.818 344.818-344.818 190.438 0 344.819 154.381 344.819 344.818 0 190.439-154.381 344.819-344.819 344.819z" fill="" p-id="5861"></path><path d="M504.5 313.259m-41.998 0a41.998 41.998 0 1 0 83.996 0 41.998 41.998 0 1 0-83.996 0Z" fill="" p-id="5862"></path><path d="M546.498 712.738c0 22.092-17.908 40-40 40h-3.996c-22.091 0-40-17.908-40-40V437.255c0-22.091 17.909-40 40-40h3.996c22.092 0 40 17.909 40 40v275.483z" fill="" p-id="5863"></path></svg>',
          style: { size: 20, color: '#666' },
        },
        themeConfig: {
          lineStyle: 'curve', //连线风格
          lineColor: '#000', //连线颜色
          // 根节点样式
          root: {
            fillColor: '#fff',
            borderColor: '#000',
            borderWidth: 1,
            color: '#666',
            fontWeight: 'none',
          },
          // 二级节点样式
          second: {
            marginY: 10,
            borderColor: '#000',
            borderWidth: 1,
          },
          // 三级及以下节点样式
          node: {
            borderColor: '#000',
            borderWidth: 1,
          },
        },
        data: localStorage.getItem('mindMapData') ? JSON.parse(localStorage.getItem('mindMapData')) : this.mindMapData,
      }
    },
    controlList() {
      return [
        {
          iconClass: this.isReadonly ? 'el-icon-edit-outline' : 'el-icon-view',
          title: '切换预览/编辑模式',
          text: this.isReadonly ? '编辑' : '预览',
          onClick: () => {
            this.isReadonly = !this.isReadonly
            this.mindMap.setMode(this.isReadonly ? 'readonly' : 'edit')
          },
        },
        {
          iconClass: 'el-icon-warning-outline',
          title: '键盘快捷键',
          text: '快捷键',
          onClick: () => {
            this.isShortcut = !this.isShortcut
          },
        },
        {
          iconClass: 'el-icon-zoom-out',
          title: '缩小图谱',
          text: '缩小',
          onClick: () => {
            this.mindMap.view.narrow()
          },
        },
        {
          iconClass: 'el-icon-zoom-in',
          title: '放大图谱',
          text: '放大',
          onClick: () => {
            this.mindMap.view.enlarge()
          },
        },
        {
          iconClass: 'el-icon-refresh-left',
          title: '恢复图谱原有尺寸',
          text: '恢复',
          onClick: () => {
            this.mindMap.view.fit()
          },
        },
        {
          iconClass: 'el-icon-full-screen',
          title: '全屏查看图谱',
          text: '全屏',
          onClick: () => {
            const element = document.body
            if (element.requestFullScreen) {
              element.requestFullScreen()
            } else if (element.webkitRequestFullScreen) {
              element.webkitRequestFullScreen()
            } else if (element.mozRequestFullScreen) {
              element.mozRequestFullScreen()
            }
            setTimeout(() => {
              this.mindMap.resize()
            }, 200)
          },
        },
        {
          iconClass: 'el-icon-folder-checked',
          title: '保存图谱',
          text: '保存',
          onClick: () => {
            localStorage.setItem('mindMapData', JSON.stringify(this.mindMapData))
            this.$message.success('保存成功')
          },
        },
      ]
    },
    newControlList() {
      if (this.isReadonly) {
        return this.controlList.filter((item) => item.text !== '快捷键' && item.text !== '保存')
      } else {
        return this.controlList
      }
    },
  },
  methods: {
    init() {
      this.mindMap = new MindMap({
        el: document.getElementById('mindMap'),
        ...this.mindMapConfig,
      })
      // 监听数据变化
      this.mindMap.on('data_change', this.getMindMapData)
      // 监听节点标签的点击事件
      this.mindMap.on('node_tag_click', this.nodeTagClick)
    },
    // 获取数据
    getMindMapData(mindMapData) {
      this.mindMapData = mindMapData
    },
    // 节点标签点击事件
    nodeTagClick(node, item) {
      console.log(item.name)
    },
  },
  created() {},
  mounted() {
    this.init()
  },
  beforeDestroy() {
    if (this.mindMap) {
      this.mindMap.off('data_change', this.getMindMapData)
      this.mindMap.off('node_tag_click', this.nodeTagClick)
      this.mindMap.destroy()
      this.mindMap = null
    }
  },
}
</script>
