      background-color: #fff;
      i {
        font-size: 16px;
        margin-right: 6px;
      }
      .shortcut {
        width: 100%;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        span {
          margin-left: 4px;
          color: #666;
        }
      }
    }
  }
}
